.row {
  @extend .cf;
  margin-left: -15px;
  margin-right: -15px;
}

[class^="col"] {
  float: left;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  min-height: 1px;
}

.col-1  { width: 8.33333333%; }
.col-2  { width: 16.66666667%; }
.col-3  { width: 25%; }
.col-4  { width: 33.33333333%; }
.col-5  { width: 41.66666667%; }
.col-6  { width: 50%; }
.col-7  { width: 58.33333333%; }
.col-8  { width: 66.66666667%; }
.col-9  { width: 75%; }
.col-10 { width: 83.33333333%; }
.col-11 { width: 91.66666667%; }
.col-12 { width: 100%; }
