/* Layout
 *************************/
.contents {
  padding: 76px 0;
  min-height: calc(100vh - 88px - 78px); }

.container, .global-header {
  max-width: 864px;
  margin: 0 auto;
  position: relative; }

/* Header
 *************************/
.global-header .site-title {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%); }

.global-navigation {
  text-align: right;
  font-size: 0; }
  .global-navigation .menu {
    display: inline-block;
    font-size: 16px; }
    .global-navigation .menu__item {
      padding-top: 29px;
      padding-bottom: 29px;
      margin-left: 34px;
      float: left; }
    .global-navigation .menu a {
      position: relative; }
      .global-navigation .menu a:after {
        content: '';
        position: absolute;
        bottom: -6px;
        left: 0;
        width: 0;
        height: 1px;
        background: #212121; }
      .global-navigation .menu a:hover:after {
        width: 100%;
        transition: width .36s ease; }

/* Footer
 *************************/
.global-footer {
  background: #212121;
  color: #707070;
  padding: 24px 0; }
  .global-footer a {
    color: #707070; }
    .global-footer a:hover {
      color: #fff; }

.global-footer-center {
  max-width: 864px;
  margin: 0 auto;
  position: relative; }

@media only screen and (max-width: 768px) {
  .global-footer__links .menu {
    margin-bottom: 16px;
    margin-right: -16px;
    text-align: center; } }

.global-footer__links .menu__item {
  float: left;
  margin-right: 16px; }
  @media only screen and (max-width: 768px) {
    .global-footer__links .menu__item {
      float: none;
      display: inline-block; } }

.global-footer__copyright {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  text-transform: uppercase;
  font-size: 14px; }
  @media only screen and (max-width: 768px) {
    .global-footer__copyright {
      text-align: center;
      position: relative;
      top: auto;
      right: auto;
      transform: initial;
      display: block;
      text-align: center; } }

/* Post
 *************************/
.post {
  position: relative;
  padding-bottom: 76px;
  margin-bottom: 76px; }
  .post:after {
    content: '';
    width: 48px;
    height: 1px;
    background: #d6d6d6;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -24px; }
  .post__title {
    margin-bottom: 0; }
    .post__title a:hover {
      transition: color .36s ease;
      color: rgba(33, 33, 33, 0.75); }

.post-meta {
  margin: 8px 0 24px;
  font-size: 0; }
  .post-meta__item {
    font-size: 14px;
    display: inline-block; }
    .post-meta__item:not(:last-child):after {
      content: '/';
      margin: 0 8px; }

.post-tags__item {
  display: inline; }
  .post-tags__item:not(:last-child):after {
    content: ',';
    margin-right: 3px; }

.post__body {
  overflow: hidden; }
  .posts .post__body p {
    color: #707070;
    margin-bottom: 0; }
  .post__body p a:before, .post__body p a:after {
    bottom: 2px; }
  .post__body blockquote, .post__body q {
    color: rgba(33, 33, 33, 0.8);
    padding-left: 2em; }
    @media only screen and (max-width: 768px) {
      .post__body blockquote, .post__body q {
        padding-left: .75em;
        border-left: solid 0.5em #f5f5f5; } }
  .post__body table, .post__body dl {
    margin-bottom: 36px; }
  .post__body th, .post__body td {
    border: solid 1px #d6d6d6;
    padding: 8px; }
  .post__body dt {
    font-weight: 700; }
  .post__body dd {
    margin-bottom: .5em; }
  .post__body li {
    margin-bottom: 4px; }
  .post__body ul,
  .post__body ol {
    margin-bottom: 36px;
    padding-left: 40px; }
  .post__body ul {
    list-style-type: disc; }
    .post__body ul ul, .post__body ul ol {
      margin-bottom: 0; }
  .post__body ol {
    list-style-type: decimal; }
    .post__body ol ul, .post__body ol ol {
      margin-bottom: 0; }
  .post__body pre {
    margin-bottom: 36px; }
    .post__body pre code {
      padding: 16px 24px; }
  .post__body code, .post__body pre {
    font-family: "Inconsolata", monospace; }
  .post__body img {
    max-width: 100%;
    height: auto; }
  .post__body .read-more {
    margin-top: 24px; }
    .post__body .read-more a {
      font-weight: 700;
      color: #212121; }

.pagination {
  position: relative;
  width: 432px;
  margin: 0 auto;
  text-transform: uppercase;
  text-align: center;
  color: #707070; }
  .pagination .prev,
  .pagination .next,
  .pagination .back {
    color: #707070;
    position: relative; }
    .pagination .prev:after,
    .pagination .next:after,
    .pagination .back:after {
      content: '';
      height: 1px;
      width: 0;
      position: absolute;
      bottom: -4px;
      left: 0;
      background: #212121; }
    .pagination .prev:hover,
    .pagination .next:hover,
    .pagination .back:hover {
      color: #212121;
      transition: color .36s ease; }
      .pagination .prev:hover:after,
      .pagination .next:hover:after,
      .pagination .back:hover:after {
        transition: width .36s ease;
        width: 100%; }
  .pagination .prev,
  .pagination .next {
    position: absolute;
    top: 0; }
  .pagination .prev {
    left: 0; }
  .pagination .next {
    right: 0; }

