/* Modules
 *************************/
.link, .post-tags a, .post__body p a {
  position: relative;
  display: inline-block; }
  .link:hover:after, .post-tags a:hover:after, .post__body p a:hover:after {
    transition: width .36s ease;
    width: 100%; }
  .link:before, .post-tags a:before, .post__body p a:before, .link:after, .post-tags a:after, .post__body p a:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: #d6d6d6; }
  .link:after, .post-tags a:after, .post__body p a:after {
    background: #212121;
    width: 0; }
